import React from 'react';
import styled from 'styled-components';

var Mediaurl =
[
"https://note.com/vuild/n/n2bdb0e882099",
"https://architects.vuild.co.jp/works/hiramiya-interior/",
"https://architects.vuild.co.jp/works/camp-pod/",
"https://note.com/vuild/n/nfef0c5a5979e",
"https://note.com/vuild/n/n1230102fd602",
"https://note.com/vuild/n/n95ad6784d821",
"https://architects.vuild.co.jp/works/nomurakougeisha-pavilion/",
"https://architecturephoto.net/97928/",
"https://architects.vuild.co.jp/works/kayac-park-office/",
"https://architecturephoto.net/98085/",
"https://architects.vuild.co.jp/works/yakushimajisugi-pavilion/",
"https://architects.vuild.co.jp/works/clt-pavilion/",
"https://architects.vuild.co.jp/works/kayac-stand/"]

var MediaTitleList = ["note","VUILD","VUILD","note","note","note","VUILD","ArchitecturePhoto","VUILD","ArchitecturePhoto","VUILD","VUILD","VUILD"]
var Medialen = Object.keys(MediaTitleList).length;

var Medialen1 = Math.ceil(Medialen/1);
var Medialen2 = Math.ceil(Medialen/2);
var Medialen3 = Math.ceil(Medialen/3);

document.documentElement.style.setProperty('--Media-num1', Medialen1);
document.documentElement.style.setProperty('--Media-num2', Medialen2);
document.documentElement.style.setProperty('--Media-num3', Medialen3);


const Div = styled.div `
:root {
  --Media-num1: 1;
  --Media-num2: 1;
  --Media-num3: 1;
}

.swiper-container{
  height: calc(100vh - 70px);
  width: 1200px;
  display: flex;
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 0);
  overflow: hidden scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin: 0;
}

@media screen and (max-width: 1200px){
  .swiper-container{
    width: 700px;
    }
  }

  @media screen and (max-width: 768px){
    .swiper-container{
      width: 300px;
      }
    }

.swiper-container::-webkit-scrollbar {
    display:none;
}

.swiper-wrapper{
  height: calc(1200px * var(--Media-num3) * 2/9);
  width: 100%;
  display: grid;
  grid-template-columns: 33.3% 33.3% 33.3%;
  flex-direction: row;
}

@media screen and (max-width: 1200px){
  .swiper-wrapper{
    height: calc(700px * var(--Media-num2) * 1/3);
    display: grid;
    grid-template-columns: 50% 50%;
    }
  }

  @media screen and (max-width: 768px){
    .swiper-wrapper{
      height: calc(300px * var(--Media-num1) * 2/3);
      display: grid;
      grid-template-columns: 100%;
      }
    }

.swiper-slide{
  height: 95%;
  width: 95%;
  display: flex;
  margin: auto;
}

.slide-image{
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
}

.hero-slider .content {
  position: relative;
  z-index: 2;
}

.tag{
  height:100%;
  width:100%;
  margin: auto;
}

.image{
  height: 100%;
  width: auto;
}

.margin-image{
  height: 100%;
  width: 50px;
}

.text{
  margin: 0;
  height: 32px;
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: rgba(254,254,254,0.5);
}

@media screen and (max-width: 768px){
  .text{
    height: 22px;
  }
}

.font{
  font-weight: 600;
  font-color: #565565;
  font-size: 30px;
}

@media screen and (max-width: 768px){
  .font{
    font-size: 20px;
    }
  }
`

var length = Medialen;
var start = 0;
var list = Array.apply(null,new Array(length)).map(function(v,i){ return start+i;});

var roop = () => {
  const items = list.map((i) =>
      <div className="swiper-slide" key={i.toString()}>
        <div className="slide-image">
          <p className="tag"><a href={Mediaurl[i]}>
          <img src={require("./Images/desktop/image" + [Medialen-i] + ".jpg")} alt="image1" className='image'></img>
          </a></p>
        </div>
        <div className="text">
          <h1 className="font">{MediaTitleList[i]}</h1>
        </div>
      </div>);

  return <div className="swiper-wrapper">
    { items }
    </div>;
};

const PhotoSliderDesktop = () => {
  return (
    <Div>
    <div className="swiper-container">
            { roop() }
      </div>
  </Div>);
};

export default PhotoSliderDesktop;
