import React from 'react';
import styled from 'styled-components';

var ProductTitleList = ["Shelf","Hi Stool","Standing Desk","Earring Case","Low Table","Desk Organizer","Tray","Hanger Rack","Bed","Lamp Stand","Bench","Lamp Shade","PC Stand","Desk","Stool"]
var Productlen = Object.keys(ProductTitleList).length;

var Productlen1 = Math.ceil(Productlen/1);
var Productlen2 = Math.ceil(Productlen/2);
var Productlen3 = Math.ceil(Productlen/3);

document.documentElement.style.setProperty('--Product-num1', Productlen1);
document.documentElement.style.setProperty('--Product-num2', Productlen2);
document.documentElement.style.setProperty('--Product-num3', Productlen3);


const Div = styled.div `
:root {
  --Product-num1: 1;
  --Product-num2: 1;
  --Product-num3: 1;
}

.swiper-container{
  height: calc(100vh - 70px);
  width: 1200px;
  display: flex;
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 0);
  overflow: hidden scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin: 0;
}

@media screen and (max-width: 1200px){
  .swiper-container{
    width: 700px;
    }
  }

  @media screen and (max-width: 768px){
    .swiper-container{
      width: 300px;
      }
    }

.swiper-container::-webkit-scrollbar {
    display:none;
}

.swiper-wrapper{
  height: calc(1200px * var(--Product-num3) * 2/9);
  width: 100%;
  display: grid;
  grid-template-columns: 33.3% 33.3% 33.3%;
  flex-direction: row;
}

@media screen and (max-width: 1200px){
  .swiper-wrapper{
    height: calc(700px * var(--Product-num2) * 1/3);
    display: grid;
    grid-template-columns: 50% 50%;
    }
  }

  @media screen and (max-width: 768px){
    .swiper-wrapper{
      height: calc(300px * var(--Product-num1) * 2/3);
      display: grid;
      grid-template-columns: 100%;
      }
    }

.swiper-slide{
  height: 95%;
  width: 95%;
  display: flex;
  margin: auto;
}

.slide-image{
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
}

.hero-slider .content {
  position: relative;
  z-index: 2;
}

.image{
  height: 100%;
  width: auto;
}

.margin-image{
  height: 100%;
  width: 50px;
}

.text{
  margin: 0;
  height: 32px;
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: rgba(254,254,254,0.5);
}

@media screen and (max-width: 768px){
  .text{
    height: 22px;
  }
}

.font{
  font-weight: 600;
  color: #565565;
  font-size: 30px;
}

@media screen and (max-width: 768px){
  .font{
    font-size: 20px;
    }
  }
`

var length = Productlen;
var start = 0;
var list = Array.apply(null,new Array(length)).map(function(v,i){ return start+i;});


var roop = () => {
  const items = list.map((i) =>
      <div className="swiper-slide" key={i.toString()}>
        <div className="slide-image">
          <img src={require("./Images/desktop/image" + [Productlen-i] + ".jpg")} alt="image1" className='image'></img>
        </div>
        <div className="text">
          <h1 className="font">{ProductTitleList[i]}</h1>
        </div>
      </div>);

  return <div className="swiper-wrapper">
    { items }
    </div>;
};

const ProductSliderDesktop = () => {
  return (
    <Div>
    <div className="swiper-container">
            { roop() }
      </div>
  </Div>);
};

export default ProductSliderDesktop;
