import React from 'react';
import styled from 'styled-components';

var PhotoTitleList = ["20.09.23","20.09.21","20.08.13","20.03.22","19.11.24","19.10.30","19.10.30","19.10.29","19.10.29","19.09.30","19.09.13","19.08.11","19.06.13","19.05.05","19.03.24","19.02.02","18.09.17","18.08.15","18.08.11","18.08.05","18.05.21","18.05.04","18.04.07","18.04.03","18.03.04","18.03.03","18.02.17","18.02.08","18.02.03","17.11.16","17.10.13","17.09.15","17.09.12","17.08.15","17.08.03","17.06.08","17.05.27","17.01.26","17.01.14"]
var Photolen = Object.keys(PhotoTitleList).length;

var Photolen1 = Math.ceil(Photolen/1);
var Photolen2 = Math.ceil(Photolen/2);
var Photolen3 = Math.ceil(Photolen/3);

document.documentElement.style.setProperty('--Photo-num1', Photolen1);
document.documentElement.style.setProperty('--Photo-num2', Photolen2);
document.documentElement.style.setProperty('--Photo-num3', Photolen3);


const Div = styled.div `
:root {
  --Photo-num1: 1;
  --Photo-num2: 1;
  --Photo-num3: 1;
}

.swiper-container{
  height: calc(100vh - 70px);
  width: 1200px;
  display: flex;
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translate(-50%, 0);
  overflow: hidden scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin: 0;
}

@media screen and (max-width: 1200px){
  .swiper-container{
    width: 700px;
    }
  }

  @media screen and (max-width: 768px){
    .swiper-container{
      width: 300px;
      }
    }

.swiper-container::-webkit-scrollbar {
    display:none;
}

.swiper-wrapper{
  height: calc(1200px * var(--Photo-num3) * 2/9);
  width: 100%;
  display: grid;
  grid-template-columns: 33.3% 33.3% 33.3%;
  flex-direction: row;
}

@media screen and (max-width: 1200px){
  .swiper-wrapper{
    height: calc(700px * var(--Photo-num2) * 1/3);
    display: grid;
    grid-template-columns: 50% 50%;
    }
  }

  @media screen and (max-width: 768px){
    .swiper-wrapper{
      height: calc(300px * var(--Photo-num1) * 2/3);
      display: grid;
      grid-template-columns: 100%;
      }
    }

.swiper-slide{
  height: 95%;
  width: 95%;
  display: flex;
  margin: auto;
}

.slide-image{
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
}

.hero-slider .content {
  position: relative;
  z-index: 2;
}

.image{
  height: 100%;
  width: auto;
}

.margin-image{
  height: 100%;
  width: 50px;
}

.text{
  margin: 0;
  height: 32px;
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: rgba(254,254,254,0.5);
}

@media screen and (max-width: 768px){
  .text{
    height: 22px;
  }
}

.font{
  font-weight: 600;
  font-color: #565565;
  font-size: 30px;
}

@media screen and (max-width: 768px){
  .font{
    font-size: 20px;
    }
  }
`



var length = Photolen;
var start = 0;
var list = Array.apply(null,new Array(length)).map(function(v,i){ return start+i;});


var roop = () => {
  const items = list.map((i) =>
      <div className="swiper-slide" key={i.toString()}>
        <div className="slide-image">
          <img src={require("./Images/desktop/image" + [Photolen-i] + ".jpg")} alt="image1" className='image'></img>
        </div>
        <div className="text">
          <h1 className="font">{PhotoTitleList[i]}</h1>
        </div>
      </div>);
  return <div className="swiper-wrapper">
    { items }
    </div>;
};

const PhotoSliderDesktop = () => {
  return (
    <Div>
    <div className="swiper-container">
            { roop() }
      </div>
  </Div>);
};

export default PhotoSliderDesktop;
